.container {
  padding: 0.2em;
}
.container.grid {
  width: 50%;
}
.container.list {
  width: 100%;
}
.video {
  list-style-type: none;
  display: flex;
  align-items: center;
  border: 1px solid lightgray;
  cursor: pointer;
  transition: transform 250ms ease-in;
  box-shadow: 11px 16px 22px -7px rgba(0, 0, 0, 0.62);
}
.video:hover {
  transform: scale(1.02);
}
.img {
  width: 40%;
  height: 100%;
}
.metadata {
  margin-left: 0.2em;
}
.title,
.channel {
  margin: 0;
  font-size: 0.8rem;
}
.channel {
  font-size: 0.6rem;
}
